import React, { useCallback, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import Button from '../../styles/Button';
import { useDispatch, useSelector } from 'react-redux';
import { login, logout, setRefresh } from '../../modules/members';
import cookie from 'react-cookies';
import refreshTokenUse from '../../lib/RefreshTokenUse';

/*
       
 */

const textMap = {
  login: '',
  register: '',
};

const AuthFormBlock = styled.div`
  h3 {
    margin: 0;
    margin-bottom: 1rem;
  }
`;

/*
      
*/
const StyledInput = styled.input`
  font-size: 1rem;
  border: none;
  padding-bottom: 0.5rem;
  outline: none;
  width: 100%;
  &:focus {
    color: $oc-teal-7;
  }
  & + & {
    margin-top: 1rem;
  }
`;

const Footer = styled.div`
  margin-top: 2rem;
  text-align: right;
  a {
    text-decoration: underline;
    &:hover {
    }
  }
`;

const ButtonWithMarginTop = styled(Button)`
  margin-top: 1rem;
`;

const AuthForm = ({ type }) => {
  const text = textMap[type];
  const navigate = useNavigate();

  //const access = useSelector((state) => state.token.access);
  const dispatch = useDispatch();
  const onLogin = useCallback(
    (accessToken, userId, userNo) => dispatch(login(accessToken, userId, userNo)),
    [dispatch],
  );
  const onLogout = useCallback(() => dispatch(logout(), [dispatch]));
  const onSetRefresh = useCallback(
    (refreshToken) => dispatch(setRefresh(refreshToken)),
    [dispatch],
  );
  const [addressList1, setAddressList1] = useState([]);
  const [addressList2, setAddressList2] = useState([]);
  const [idDuplicateChecked, setIdDuplicateChecked] = useState(false);
  const [idDuplicateMessage, setIdDuplicateMessage] = useState(false);

  useEffect(() => {
    if(type=="register"){
      axios({
        method: 'get',
        url: '/auth/getAddress1List',
      }).then((response) => {
        response.data.forEach((item) => {
          setAddressList1((prev) => {
            return [...prev, item];
          });
        });
      });
    }
  }, []);
  function updateAddressList2() {
    axios({
      method: 'get',
      url: '/auth/getAddress2List',
      params: {
        address1: address1.current.value,
      },
    }).then((response) => {
      setAddressList2([]);
      response.data.forEach((item) => {
        setAddressList2((prev) => {
          return [...prev, item];
        });
      });
    });
  }

  function idDuplicateCheck(){
    axios({
      method:'get',
      url:'/auth/isMember',
      params:{
        userId: idRef.current.value
      }
    }).then((response)=>{
      console.log(response);
      setIdDuplicateChecked(response.data);
      setIdDuplicateMessage(response.data ? "  ." : "  .");
    });
  }

  function doRegister(e) {
    //e.preventDefault();

    // axios.post("/auth/join", null, {params : {~}})
    //   data ,   Query  .
    //  '@RequestParam'       .
    if(!idDuplicateChecked){
      axios({
        method: 'post',
        url: '/auth/member',
        params: {
          userId: idRef.current.value,
          pw: pwRef.current.value,
          userNickName: nickNameRef.current.value,
          address1 : address1.current.value,
          address2 : address2.current.value
        },
        timeout: 5000,
        //responseType:"json" // or "stream"
      })
        .then(function (response) {
          navigate('/login');
          //return JSON.parse(response);
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  }

  function doLogin(e) {
    axios({
      method: 'get',
      url: '/auth/login',
      params: {
        userId: idRef.current.value,
        pw: pwRef.current.value,
      },
      timeout: 5000,
    })
      .then(function (response) {
        let refreshToken = cookie.load('CodedRefreshToken');
        refreshToken = refreshToken.substr(
          'Bearer '.length,
          refreshToken.length,
        );
        onLogin(response.data.accessToken, response.data.userId, response.data.userNo);
        onSetRefresh(refreshToken);
      })
      .catch(function (e) {
        console.log(e);
        onLogout();
      });
  }

  function doRefrshTest() {
    axios({
      type: 'GET',
      url: '/auth/refresh',
    })
      .then(function (response) {
        //  
        onLogin(response.data);
        let refreshToken = cookie.load('CodedRefreshToken');
        refreshToken = refreshToken.substr(
          'Bearer '.length,
          refreshToken.length,
        );
        onSetRefresh(refreshToken);
      })
      .catch(function (e) {
        console.log(e);
        onLogout();
        //history.go('/login');
      });
  }

  function doKakaoLogin() {
    axios({
      method: 'get',
      url: '/login/oauth2/kakao/codeInfo',
    })
      .then(function (response) {
        const KAKAO_AUTH_URL = `https://kauth.kakao.com/oauth/authorize?client_id=${response.data.client_id}&redirect_uri=${response.data.redirect_uri}&response_type=code`;
        window.location.href = KAKAO_AUTH_URL;
      })
      .catch(function (e) {
        console.log(e);
      });
  }

  function doNaverLogin() {
    axios({
      method: 'get',
      url: '/login/oauth2/naver/codeInfo',
    })
      .then(function (response) {
        const NAVER_AUTH_URL = `https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=${response.data.client_id}&redirect_uri=${response.data.redirect_uri}&state=test`;
        window.location.href = NAVER_AUTH_URL;
      })
      .catch(function (e) {
        console.log(e);
      });
  }

  const nickNameRef = useRef(null);
  const idRef = useRef(null);
  const pwRef = useRef(null);
  const pwConfirmRef = useRef(null);
  const address1 = useRef(null);
  const address2 = useRef(null);

  return (
    <AuthFormBlock>
      <h3>{text}</h3>
      {type === 'register' && (
        <StyledInput
          type="text"
          autoComplete="name"
          name="userNickName"
          placeholder=""
          ref={nickNameRef}
        />
      )}
      <StyledInput
        type="text"
        autoComplete="username"
        name="userId"
        placeholder=""
        ref={idRef}
        onChange={type==="register" ? idDuplicateCheck : null}
      />
      <div>{idDuplicateMessage}</div>
      <StyledInput
        autoComplete="new-password"
        name="pw"
        placeholder=""
        type="password"
        ref={pwRef}
      />
      {type === 'register' && (
        <>
          <StyledInput
            autoComplete="new-password"
            name="pwConfirm"
            placeholder=" "
            type="password"
            ref={pwConfirmRef}
          />
          <select ref={address1} onChange={updateAddressList2}>
            {addressList1.map((item, index) => {
              return <option key={index}>{item}</option>;
            })}
          </select>
          <select ref={address2}>
            {addressList2.map((item, index) => {
              return <option key={index}>{item}</option>;
            })}
          </select>
        </>
      )}
      {/* {type === 'login' && (
        <>
          <button onClick={doKakaoLogin}> </button>
          <button onClick={doNaverLogin}> </button>
          <button onClick={doRefrshTest}> </button>
        </>
      )} */}
      <ButtonWithMarginTop
        cyan={true}
        fullWidth
        onClick={type === 'register' ? doRegister : doLogin}
      >
        {text}
      </ButtonWithMarginTop>
      <Footer>
        {type === 'login' ? (
          <Link to="/signup"></Link>
        ) : (
          <Link to="/login"></Link>
        )}
      </Footer>
    </AuthFormBlock>
  );
};

export default AuthForm;
