import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { styled } from 'styled-components';
import FeedPostDetail from '../FeedPostDetail/FeedPostDetail';

const FeedPostOuter = styled('div')`
  margin: auto;
  width: 80%;
  border: 1px solid black;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
`;

function FeedList() {
  const [cpage, setCpage] = useState(1);
  const [feedPost, setFeedPost] = useState([]);
  const [thumbNail, setThumbnail] = useState([]);
  const [member, setMember] = useState([]);
  const [userProfile, setUserProfile] = useState([]);
  const [hashTagList, setHashTagList] = useState([]);

  const addFeedList = () => {
    axios({
      method: 'GET',
      url: '/feedpost/selectAllFeedPost/',
      params: {
        cpage: cpage,
      },
    })
      .then((resp) => {
        console.log(resp.data);

        const {
          feedPostList,
          thumbNailList,
          memberList,
          userProfileList,
          hashTagLists,
        } = resp.data;

        console.log({ thumbNailList });

        setFeedPost(feedPostList);
        setThumbnail([...thumbNailList]);
        setUserProfile([...userProfileList]);
        setMember([...memberList]);
        setHashTagList([...hashTagLists]);
        setCpage(cpage + 1);
      })
      .catch((error) => console.log(error));
  };

  window.onscroll = function () {
    if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
      addFeedList();
    }
  };

  useEffect(() => {
    console.log(' ');
    addFeedList();
    return () => {
      console.log(' ');
    };
  }, []);

  return (
    <FeedPostOuter>
      {feedPost.map((e, i) => (
        <FeedPostDetail
          feedPost={e}
          thumbNail={thumbNail[i]}
          member={member[i]}
          userProfile={userProfile[i]}
          hashTagList={hashTagList[i]}
        ></FeedPostDetail>
      ))}
    </FeedPostOuter>
  );
}

export default FeedList;

