import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { styled } from 'styled-components';
import FeedPostDetail from '../../component/FeedPostDetail/FeedPostDetail';

const FeedPostOuter = styled('div')`
  margin: auto;
  width: 1202px;
  border: 1px solid black;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
`;

const FeedListByHashs = () => {
  const [test, setTest] = useState([]);
  const [cpage, setCpage] = useState(1);

  const addFeedList = () => {
    axios({
      method: 'GET',
      url: '/feedpost/searchByHashs',
      params: {
        hashTag: 'a',
      },
    })
      .then((resp) => {
        let temp = [];
        console.log(resp.data);
        resp.data.forEach((i) => {
          temp = [...temp, { id: i.feedPostId, body: i.body }];
        });
        setTest([...test, ...temp]);
        setCpage(cpage + 1);
      })
      .catch((resp) => console.log(resp));
  };

  window.onscroll = function () {
    if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
      addFeedList();
    }
  };

  useEffect(() => {
    addFeedList();
  }, []);

  return (
    <FeedPostOuter>
      {test.map((i) => (
        <FeedPostDetail key={i.id} id={i.id} body={i.body} />
      ))}
    </FeedPostOuter>
  );
};

export default FeedListByHashs;

