import React from 'react';
import styled from 'styled-components';
import { Link, withRouter } from 'react-router-dom';

const ProfileFormBlock = styled.table`
  width: 50%;
  height: 400px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.1);
`;

const ProfileForm = ({ history }) => {
  return (
    <ProfileFormBlock>
      <div className="container">
        <div className="imageBox">
          <div className="proImage">
            <img src=""></img>
            //   
          </div>
          <div className="btns">
            <button className="profileImageChangebtn"></button>
          </div>
        </div>

        <div className="infoBox">
          <div className="infocontents">
            <ul>
              <li>nick : </li>
              <li>id : </li>
              <li>pw : </li>
              <li>bio : </li>
              <li>hashtag : </li>
              <li>location : </li>
            </ul>
          </div>
          <div className="btns">
            <button className="PwChangeBtn"> </button>
            <button className="EditBtn"></button>
            <button className="EditCancelBtn"></button>
            <button className="EditComBtn"></button>
            <button className="MemberOutBtn"></button>
          </div>
        </div>
      </div>
    </ProfileFormBlock>
  );
};

export default ProfileForm;
