import React from 'react';
import style from './DMPage.module.css';
import { Stomp } from '@stomp/stompjs';
import { useEffect, useState } from 'react';

const DMPage = () => {
  useEffect(() => {
    const socket = new WebSocket('ws://192.168.50.221:9999/chatchat');
    const stompClient = Stomp.over(socket);

    stompClient.connect(
      {},
      function () {
        console.log(' ');
        stompClient.subscribe('/topic/dialog', function (message) {
          console.log(' '); // message
        });
      },
      function (error) {
        console.log(' ');
      },
    );
  }, []);

  return (
    <div className={style.chatLayout}>
      <div className={style.chatBox}>
        <div className={style.messageArea}></div>
        <div className={style.inputArea}>
          <input
            type="text"
            className={style.chat}
            placeholder=" "
          />
        </div>
      </div>
    </div>
  );
};

export default DMPage;
